.error {
  font-weight: bold;
  color: red; }

.c-contact__label.confirm__label {
  border-bottom: 1px dashed rgba(0, 0, 0, 0.5); }

.example {
  display: none; }

.p-contact__btn-back {
  padding: 8px 0;
  width: 150px;
  margin: 40px auto 0;
  display: block;
  background-color: transparent;
  border: none;
  color: rgba(0, 0, 0, 0.5);
  text-decoration: underline; }
