    color: #178fff; }

.p-contact-complete {
  display: flex;
  justify-content: center; }

.p-contact-complete__btn {
  display: inline-block;
  padding: 8px 16px;
  text-decoration: underline; }
  .p-contact-complete__btn:hover {
